body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  background-color: #563d7c !important;
}
.image-item {
  width: 100%;
  height: 100%;
  border: 2px solid #f5f7fa;
}
ul,
li {
  list-style-type: none;
}
.grid-list-container {
  padding-left: 20%;
  padding-right: 20%;
  height: 100%;
  border: 2px solid #e2e2ff;
  /* box-shadow: 10px -4px 18px 2px rgba(226,226,255,0.48); */
  min-height: 600px;
}

.grid-list-container {
  width: 100%;
  float: left;
  background-color: #343a40;
}
.actul-img-left {
  padding: 0;
}
.actul-img-left li,
.resize-image li {
  width: auto;
  padding: 5px !important;
  float: left;
}
.resize-image li {
  min-height: 210px;
  max-height: 210px;
}
.resize-image {
  padding: 0 !important;
  margin-bottom: 0 !important;
}
.img-zom {
  zoom: 30%;
}
.crop-ele {
  min-height: 500px;
}
.preview-ele {
  min-height: 500px;
}
.ant-modal-body {
  padding: 0px 0px 0px 0xp;
  text-align: center;
}
.file-up {
  position: relative;
  overflow: hidden;
  color: #7952b3;
  border-color: #7952b3;
}
.file-up:hover {
  color: #fff;
  background-color: #7952b3;
  border-color: #7952b3;
}
.file-up input {
  position: absolute;
  font-size: 50px;
  opacity: 0;
  right: 0;
  top: 0;
}

